/* FileName: player.scss */

.player {
    min-height: 15vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.time-control {
    width: 50%;
    display: flex;
    align-items: center;
    input {
        width: 100%;
        background-color: transparent;
        cursor: pointer;
    }
    p {
        padding: 1rem;
        font-weight: 700;
    }
}
.play-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 30%;
    svg {
        cursor: pointer;
    }
}
input[type="range"]:focus {
    outline: none;
}
input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
}
.track {
    background: rgb(151, 205, 223);
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}
.animate-track {
    background: rgb(238, 209, 209);
    width: 100%;
    height: 100%;
    position: absolute;  
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
}
@media screen and (max-width: 768px) {
    .time-control {
        width: 90%;
    }
    .play-control {
        width: 80%;
    }
}